<template>
  <div class="mainWindow">
    <div class="networkWindow">
      <Header class="grid-item-header">bakalarkska praca</Header>
      <Paths class="grid-item-path" v-model="paths">cesta</Paths>
      <HelloWorld
        msg="Welcome to Your Vue.js App"
        class="mojeee grid-item-network"
        @customChange="logChange"
        @custom2="logCh"
        :text="txt"
      />
      <Structure
        class="grid-item-structure"
        :clickedNodes="clickedNodes"
        @removeNode="getEmit"
        >vpravo</Structure
      >
      <div class="grid-item-textarea">
